/* put styles here */
body {
  background-color: rgb(134, 163, 255);
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

h1 {
  text-align: center;
}

.container {
  width: 80%;
  margin: 0 auto;
}

div .startBtn {
  display: block;
  margin: 0 auto;
}

.buttons {
  display: flex;
  margin: 0 auto;
  justify-content: space-between;
}

.btn {
  color: rgb(136, 247, 179);
  width: 200px;
  padding: 10px 0;
  margin: 15px;
  border-radius: 5px;
  border: 2px solid rgb(9, 65, 248);
  background-color: rgb(96, 132, 253);
  font-size: 18px;
  font-weight: 700;
  cursor: pointer;
  transition: 0.5s ease-out;
}

.btn:hover {
  color: rgb(9, 65, 248);
  background-color: rgb(136, 247, 179);
}

.listOfGoods {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0 auto;
  width: 50%;
}

.listOfGoods li {
  list-style: none;
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  border: 2px solid rgb(9, 65, 248);
  background-color: rgb(136, 247, 179);
  color: rgb(9, 65, 248);
  font-size: 18px;
  font-weight: 700;
}

.hidden {
  display: none;
}
